* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

body {
    padding: 50px;
    background: linear-gradient(to top, #bed6d8, lavender);
    font-family: sans-serif;
    min-height: 100vh;
}

.mainContainer {
    max-width: 500px;
    min-height: 600px;
    background: #161a2b;
    text-align: center;
    margin: 5% auto;
    border-radius: 10px;
    padding: 32px;
    position: static;
}

.todoForm {
    margin-bottom: 32px;
    border: 2px solid #846ab7;
    border-radius: 10px;
}

.todoForm input {
    margin: 40px 0px;
    outline: none;
    width: 320px;
    background: transparent;
    color: #fff;
    border-radius: 4px 0 0 4px;
}

.todoForm input[type=text] {
    padding: 14px 32px 14px 16px;
    border: 2px solid #5d0cff;
}

.todoForm input[type=range] {
    margin: 0;
    height: 7px;
    border-radius: 4px;
    -webkit-appearance: none;
    background: linear-gradient(45deg, rgb(18, 212, 50), rgb(56, 19, 192), rgb(250 0 134 / 1));
}

.priorityValue {
    color: wheat;
    font-size: 16px;
    margin-bottom: 15px;
}

.todoForm input::placeholder {
    color: #e2e2e2;
}

.todoButton {
    padding: 16px;
    width: 80px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient( 90deg, rgba(93, 12, 255, 1) 0%, rgba(155, 0, 250, 1) 100%);
    color: #fff;
    text-transform: capitalize;
}

.todoItem {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px auto;
    color: #fff;
    padding: 16px;
    border-radius: 5px;
    width: 90%;
}

.theList {
    padding-left: 0;
    width: 250px;
}

.theList li {
    color: #333;
    background-color: rgb(255, 255, 255, .5);
    padding: 15px;
    margin-bottom: 15px;
    border-radius: 5px;
    list-style: none;
}

.theList li:before {
    content: 'x';
    color: #fff;
    font-weight: 300;
    font-family: Arial, sans-serif;
}

.addTask {
    text-align: center;
    padding: 10px;
    font-size: 16px;
    margin: 10px;
    margin-right: 0;
    background-color: #0066FF;
    color: #FFF;
    border: 2px solid #0066FF;
    width: 90px;
}

.addTask:hover {
    background-color: #003399;
    border: 2px solid #003399;
    cursor: pointer;
}

.deleteIcon {
    font-size: 25px;
    cursor: pointer;
}

.todoItem .inputs {
    margin: 0 10px;
}

.todoItem input[type=checkbox] {
    -ms-transform: scale(2);
    -moz-transform: scale(2);
    -webkit-transform: scale(2);
    -o-transform: scale(2);
    transform: scale(2);
    padding: 10px;
    cursor: pointer;
}

.todoItem input[type=checkbox]:before {
    position: relative;
    display: block;
    width: 11px;
    height: 11px;
    border: 1px solid #999ba4;
    content: "";
    background: #FFF;
  }

  .todoItem input[type="checkbox"]:after {
    position: relative;
    display: block;
    left: 3px;
    top: -10px;
    width: 7px;
    height: 7px;
    border-color: #B3B3B3 #dcddde #dcddde #B3B3B3;
    content: "";
    background-position: center;
  }
  
  .todoItem input[type="checkbox"]:checked:after {
    background-image: url('data:image/png;base64,');
  }
  
  .todoItem input[type="checkbox"]:not(:disabled):checked:hover:after {
    background-image: url('data:image/png;base64,');
  }
  

.todoItem input[type=number] {
    margin-right: 10px;
    width: 40px;
}

.filterBlock {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;
    flex-wrap: wrap;
}

.search input {
    outline: none;
    background: transparent;
    color: #fff;
    border-radius: 4px 0 0 4px;
    padding: 14px 32px 14px 16px;
    border: 2px solid #5d0cff;
}

.search {
    position: relative;
}

.closeSearch::after {
    content: 'x';
    color: #ced2d3;
    font-size: 25px;
    font-weight: 300;
    font-family: Arial, sans-serif;
    display: inline-block;
    content: "\00d7";
    opacity: 0;
    position: absolute;
    top: 30px;
    right: 10px;
    top: 3px;
    cursor: pointer;
}


.closeActive::after {
    opacity: 1;
}

.filter select {
    outline: none;
    background: #161a2b;
    color: #e2e2e2;
    border-radius: 4px 0 0 4px;
    padding: 14px 32px 14px 16px;
    border: 2px solid #5d0cff;
}

.todoItemDone {
    background-color: rgb(195, 195, 195);
    color: #605d5d;
}

.nameTodoDone {
    text-decoration: line-through;
}

.sort {
    flex-basis: 100%;
    margin: 20px;
    color: wheat;
}

.sort input {
    margin: 20px;
}

.sort input::after {
    width: 15px;
    height: 15px;
    border-radius: 4px;
    top: -1px;
    left: -1px;
    position: relative;
    background-color: #161a2b;
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid #e2e2e2;
}

.sort input:checked::after {
    width: 15px;
    height: 15px;
    border-radius: 4px;
    top: 0px;
    left: 0px;
    position: relative;
    background-color: #d1d3d1;
    content: '';
    display: inline-block;
    visibility: visible;
    border: 2px solid #5d0cff;
}